body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main{
   background: linear-gradient(#b9e3e7, #90c0d1);
  padding-top:10px;
}

.searchContainer{
  margin-bottom:10px;
  border-radius: 10px;
  padding-top:15px;
}

.colorBox{
  background-color: #e36387;
  border-radius: 15px;
  padding:10px 0;
  margin-bottom:10px;
  text-align: center;
}

.wrapper{
  max-width: 1350px;
  width:90%;
  margin: 0 auto;
}

h2,
p{
  color:#fff;
  font-family: 'open sans';
  text-transform: normal;
}

p{
  font-size:2rem;
}

h1{
  font-size: 6.5rem;
  font-weight:200;
  margin-top:10%;
  margin-bottom:5%;
  font-family: 'helvetica neue';
  color:#fff;
}

span{
  text-transform: uppercase;
}

.iconContainer{
  display:flex;
  justify-content: space-evenly;
}

.weatherIcons{
  font-size: 6rem;
  margin: 5% 0;
  transition:400ms ease;
  padding:5px;
  color:#fff;
}

.weatherIcons:hover{
  scale:1.2;
}

input[type=text],
button{
  border: 3px solid transparent;
  border-radius: 15px;
  padding:10px;
  transition: ease-in 300ms;
}

input[type=text],
button{
  display:inline-block;
  margin: 10px auto;
}

input[type=text]{
  width: 60%;
  text-align: center;
  padding:20px;
}

input[type=text]:focus,
input[type=text]:hover{
  border: solid 3px #F2AAAA;
}


button{
  width:30%;
  color:#fff;
  background:#F2AAAA;
}

button:hover,
button:focus{
  color: #e36387;
  border: solid 3px #F2AAAA;
  background: #fff;
}

.searchContainer{
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-self: center;
  text-align: center;
}

.dateTime{
  text-align: center;
  margin: 5%;
}

.modal{
  opacity:0.8;
}

.modal p{
  margin:10px;
}

.row1,
.row2{
  flex: 1 0 auto;
  display:flex;
}

.square{
  min-width: 50%;
  text-align: center;
  padding:10px;
  text-transform: capitalize;
  min-height: 150px;
  transition: 200ms ease;
}

.square:hover{
  scale:1.1;
}

.squareFour{
  display:flex;
  justify-content: space-around;
}

.copyright{
  text-align: center;
  background-color: #222;
  width:100%;
  padding:10px;
}

.copyright p {
  font-size:1rem;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

